/* header.css */

/* General styling for the header container */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: lightblue;
    padding: 10px;
  }
  
  /* Styling for the logo */
 
  

  .navBar {
    display: flex;
    gap: 30px;
    margin-right: 50px;
  }
  
  /* Styling for navigation links */
  .navBar a {
    text-decoration: none;
    color: white; /* Text color for navigation links */
    font-weight: bold; /* Adjust font weight as needed */
    transition: color 0.3s ease; /* Add transition effect for smooth color change */
  }
  
  /* Change link color on hover */
  .navBar a:hover {
    color: lightgray; /* Adjust hover color as needed */
  }

  #hamburger{
    display: none;
    cursor: pointer;
    background-color: white;
    border-radius: 5px;
    padding: 8px;
    transition: background-color 0.3s ease;
}

/* header.css */

/* ... Your existing styles */

.dropdown {
  display: none;
  position: absolute;
  background-color: #fff;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  z-index: 1;
}

.navBar:hover .dropdown {
  display: block;
}

.dropdown a {
  color: #333;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown a:hover {
  background-color: #f8f8f8;
}






  @media screen and (max-width:800px) {
    #desktop-menu{
        display: none;
      }

    #hamburger{
        display: block;
    }

    .header {
      display: flex;
      justify-content: space-between;
    }

   
    
  }
  